import { faCheck, faWindowClose } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React from "react";
import styled from "styled-components";

const Container = styled.div`
    position: absolute;
    width: 80%;
    min-height: 80%;
    color: white;
    z-index: 1;
`;

const Background = styled.div`
    position: absolute;
    top: 0;
    width: 100%;
    height: 80vh;
    background: black;
    opacity: 60%;
`;

const Terms = styled.div`
    text-align: center;
    word-break: keep-all;
    line-height: 140%;
    display: flex;
    justify-content: center;
    align-items: center;
    color: white;
    height: 80vh;
    background: black;
    opacity: 100%;
`;

const CloseBtn = styled.button`
    position: absolute;
    top: 10px;
    right: 10px;
    background: none;
    border: none;
    font-size: 1.2rem;
    color: white;
    :hover {
        cursor: pointer;
    }
`;

const TermModal = ({ closeModal }) => {

    const onClickClose = () => {
        closeModal(false);
    }

    return (
        <Container>
            <CloseBtn onClick={onClickClose}>
                <FontAwesomeIcon icon={faCheck} />
            </CloseBtn>
            <Terms>
                 <br /><br />

                ' '  '' <br /><br />

                1.         . <br />
                2.          . <br />
                3.             ,    . <br />
                4.   ,     . <br />
                5.        . <br />
            </Terms>
        </Container>
    )
}

export default TermModal;