import React, { useEffect, useState } from "react";
import styled, { keyframes } from "styled-components";

const LoadingBox = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    color: white;
`;

const Blink = keyframes`
    0% {
        opacity: 100%;
    }
    50% {
        opacity: 30%;
    }
    100% {
        opacity: 100%;
    }
`;

const Skew = keyframes`
    0% {
        transform: skew(0)
    }
    33% {
        transform: skew(-1deg)
    }
    66% {
        transform: skew(1deg)
    }
    100% {
        transform: skew(0)
    }
`;

const DotBlink = keyframes`
    0% {
        opacity: 0%;
    }
    20% {
        opacity: 0%;
    }
    40% {
        opacity: 100%;
    }
    60% {
        opacity: 100%;
    }
    80% {
        opacity: 100%;
    }
    100% {
        opacity: 100%
    }
`;

const Title = styled.h1`
    position: absolute;
    bottom: 60vh;
    display: flex;
    margin-bottom: 30px;
    font-family: Kyobo Handwriting;
`;

const Bulb = styled.img`
    position: absolute;
    height: 35vh;
    width: auto;
    top: 0;
    animation: ${Blink} 2s ease-in-out infinite;
`;

const Drawer = styled.img`
    position: absolute;
    animation: ${Skew} 2s ease-in-out infinite ;
    height: 35vh;
    width: auto;
    opacity: 80%;
    bottom: 25vh;
    object-fit: cover;
`;

const Dot = styled.div`
    animation: ${DotBlink} 2s infinite;
    :nth-child(2) {
        animation-delay: 0.5s;
    }
    :last-child {
        animation-delay: 1s;
    }
`;

const AnswerCount = styled.span`
    position: absolute;
    bottom: 15vh;
`;

const comment = [
    "  ", 
    "    ", 
    "   ", 
    "   ", 
    "     ", 
    "  ",
    "   ",
    "   ",
    "   ",
    "   ",
    "   ",
    ]

const Loading = () => {
    const [num, setNum] = useState("");
    const [answerCount, setAnswerCount] = useState('');

    useEffect(() => {
        localStorage.getItem("drawerAnswers") && setAnswerCount(JSON.parse(localStorage.getItem("drawerAnswers")));
        setNum(Math.floor(Math.random()*comment.length));
    }, [])

    return (
       <LoadingBox>
           <Bulb class="loading__bulb" src="https://cdn.jsdelivr.net/gh/vangona/answer_community/src/img/loading_bulb.png" alt="" />
           <Title>
               {comment[num]}<Dot>.</Dot><Dot>.</Dot><Dot>.</Dot>
            </Title>
           <Drawer class="loading__drawer" src="https://cdn.jsdelivr.net/gh/vangona/answer_community/src/img/loading_drawer.png" alt="" />
           <AnswerCount> {answerCount}  .</AnswerCount>
       </LoadingBox>
    )
}

export default Loading;